:root {
  --primary-text-color: #000;
  --accent-color: #e6a8ea;
  --primary-background-color: #fff;
  --active-green: #0dcd2a;
  --hover-green: #077c3c;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  width: 120px;
  height: 40px;
  cursor: pointer;
  background-color: var(--accent-color);
  border: 2px solid #9e26b9;
  border-radius: 5px;
  padding: 0;
  font-family: inherit;
}

button:hover {
  background-color: #9e26b9;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--primary-background-color);
  color: var(--primary-text-color);
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: var(--accent-color);
}

.no-scroll {
  overflow: hidden;
}

.header {
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  background-color: #31bbac;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.header .nav-left, .header .nav-right {
  align-items: center;
  display: flex;
}

.header .nav-left a, .header .nav-right a {
  color: #fff;
  margin-right: 20px;
  padding: 10px;
  text-decoration: none;
}

.header .nav-right a {
  margin-right: 0;
}

.header .nav-left .current, .header .nav-right .current, .header a:hover {
  color: #9e26b9;
}

.list {
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  gap: 80px 20px;
  padding: 20px;
  display: grid;
}

.list-item {
  width: 100%;
  max-width: 300px;
  text-align: center;
  border: 2px solid var(--accent-color);
  background-color: var(--primary-background-color);
  border-radius: 10px;
  padding: 30px;
}

.list-img {
  width: 100%;
}

.list-title {
  margin-top: 10px;
  margin-bottom: 10px;
}

.list-text {
  margin-top: 20px;
}

.list-link {
  margin-bottom: 10px;
  font-size: 18px;
}

.list-btn {
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
  display: flex;
}

.modal-item {
  width: 600px;
  background-color: #ada861;
  padding: 14px 20px;
}

.quantity-square {
  width: 40px;
  height: 40px;
}

.quantity {
  width: 100%;
  height: 100%;
  text-align: center;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.form-search {
  margin-top: 20px;
  margin-bottom: 20px;
}

.input-search {
  color: #999;
  width: 160px;
  height: 34px;
  background: #f2f4f5;
  border: none;
  border-radius: 21px;
  outline: none;
  padding: 0 70px 0 10px;
  line-height: 18px;
  position: relative;
}

.button-search {
  width: 70px;
  height: 34px;
  cursor: pointer;
  background-color: #e3dede;
  border: 1px solid #adabad;
  border-radius: 21px;
  padding: 0;
  font-family: inherit;
  position: absolute;
  top: 80px;
  left: 200px;
}

.button-search:hover {
  background-color: #c1bcbc;
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

